import style from "./Nav.module.css";
import {HashLink} from "react-router-hash-link";
import toScroll from "../../../../../pipes/toScroll";
import {useSelector} from "react-redux";

const Nav = () => {
    const allProjects = useSelector(state => state.projects.projects);
    return (
        <div className={style.container}>
            <HashLink scroll={(element) => toScroll(element)} to={"./#body"}></HashLink>
            <HashLink scroll={(element) => toScroll(element)} to={"./#aboutUs"}> </HashLink>
            <HashLink scroll={(element) => toScroll(element)} to={"./#ourProjects"}>
                {allProjects.length > 1 ? <> </> : <> </>}
            </HashLink>
            <HashLink scroll={(element) => toScroll(element)} to={"./#advice"}></HashLink>
            <HashLink scroll={(element) => toScroll(element)} to={"/details"}></HashLink>
            <HashLink scroll={(element) => toScroll(element)} to={"./#footer"}></HashLink>
        </div>
    )
}
export default Nav;
