import style from "./VisitCounter.module.css";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faUsers} from "@fortawesome/free-solid-svg-icons";
import {useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";
import axios from "axios";

const VisitCounter = () => {
    const navigate = useNavigate();
    const [dataCounterOfVisits, setDataCounterOfVisits] = useState();
    useEffect(() => {
        axios(process.env.REACT_APP_LINKTOAPI + '/visits/stats').then(
            resp => {
                setDataCounterOfVisits(resp.data);
            }
        ).catch(error => {
            navigate('/error');
        });
    }, []);
    if (!dataCounterOfVisits) {
        return <h4>Loading....</h4>
    }
    return (
        <div className={style.container}>
            <div className={style.blockForTitleAndIcon}>
                <h4>  :</h4>
                <FontAwesomeIcon icon={faUsers} className={style.icon}/>
            </div>
            <div className={style.blockForVisits}>
                <span>: {dataCounterOfVisits.activeNow}</span>
                <span>:{dataCounterOfVisits.totalToday}</span>
                <span>: {dataCounterOfVisits.yesterdayStats}</span>
                <span> : {dataCounterOfVisits.thisWeekStats}</span>
                <span> : {dataCounterOfVisits.thisMonthStats}</span>
                <span> : {dataCounterOfVisits.thisYearStats}</span>
                <span>: {dataCounterOfVisits.totalStats}</span>
            </div>
        </div>
    )
}
export default VisitCounter;
