import style from "./OpenHours.module.css";
import {faClock} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {useSelector} from "react-redux";

const OpenHours = () => {
    const data = useSelector(state => state.websiteDates.websiteDates?.footer?.workShedule);
    if (!data) {
        return <h4>Loading....</h4>
    }
    return (
        <div className={style.container}>
            <div className={style.blockForTitleAndIcon}>
                <h4> :</h4>
                <FontAwesomeIcon icon={faClock} className={style.icon}/>
            </div>
            <div className={style.blockForOpeningHours}>
                <span>: {data.monday.isOpen ? `${data.monday.open}-${data.monday.close}` : ''}</span>
                <span>: {data.tuesday.isOpen ? `${data.tuesday.open}-${data.tuesday.close}` : ''}</span>
                <span>: {data.wednesday.isOpen ? `${data.wednesday.open}-${data.wednesday.close}` : ''}</span>
                <span>: {data.thursday.isOpen ? `${data.thursday.open}-${data.thursday.close}` : ''}</span>
                <span>: {data.friday.isOpen ? `${data.friday.open}-${data.friday.close}` : ''}</span>
                <span> - {data.saturday.isOpen ? `${data.saturday.open}-${data.saturday.close}` : ''}</span>
                <span> - {data.sunday.isOpen ? `${data.sunday.open}-${data.sunday.close}` : ''}</span>
            </div>
        </div>
    )
}
export default OpenHours;
